/* eslint-disable */

import React, {useState, useEffect} from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './detail.scss'

let 박스 = styled.div`
  padding : 20px;
`;
let 제목 = styled.h4`
  font size : 25px;
`;



function Detail(props) {

  let [Alert_stat,Alert_stat_chg] = useState(true)
  let [inputData, inputDatachg] = useState('');

  useEffect(()=>{
    let 타이머 = setTimeout(()=>{ Alert_stat_chg(false)  }, 2000)
    return ()=> { clearTimeout(타이머) }

  },[]);

  let { id } = useParams();
  let history = useHistory();

  return (
    <div className="container">
      <박스>
        <제목 className="red">Detail</제목>
      </박스>


      <input onChange={ (e)=>{ inputDatachg(e.target.value) }} />

      {
        Alert_stat === true
        ?  <Alert/>
        : null
      }

      <div className="row">
        <div className="col-md-6">
          <img src={`https://codingapple1.github.io/shop/shoes${Number(id)+1}.jpg`} width="100%" alt="shoes1" />
        </div>
        <div className="col-md-6 mt-4">
          {props.shoes.map((v)=>{
            return (
              v.id===Number(id)
              ? (
              <div key={v.title}>
                <h4 className="pt-5">{v.title}</h4>
                <p>{v.content}</p>
                <p>{v.price}</p>
                <Info stock={props.stock[id]} />
              </div>
              ) : null
            )
          })}
          <button className="btn btn-danger" onClick={()=>{
            const temp = [...props.stock]
            temp[id] -= 1
            props.stockchg(temp)
          }}>주문하기</button> 
          <button className="btn btn-danger" onClick={()=>{
            history.goBack();
          }}>뒤로가기</button> 
        </div>
      </div>
    </div> 
  )
}

function Info(props) {
  return (
    <p>재고 : {props.stock}</p>
  )

}

function Alert() {
   return <div className="my-alert2">
            <p>재고가 얼마 남지 않았습니다.</p>
          </div>
}


export default Detail