/* eslint-disable*/ 

import React, { useState } from 'react';
import {Navbar, Container, Nav, NavDropdown, Button, Row, Col} from 'react-bootstrap';
import './App.css';
import Data from './data'
import Detail from './Detail'
import axios from 'axios';

import { Link, Route, Switch } from 'react-router-dom';

function App() {

  let [shoes, shoes_chg] = useState(Data)
  let [재고, 재고변경] = useState([10,11,13]);

  return (
    <div className="App">
        <Navbar bg="light" expand="sm">
          <Container>
            <Navbar.Brand href="#home">OkShop</Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
              <Nav className="me-auto">
                <Nav.Link as={Link} to="/" > Home</Nav.Link>
                <Nav.Link as={Link} to="/detail/0" >Detail</Nav.Link>
                <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                  <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                  <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                  <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                  <NavDropdown.Divider />
                  <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
                </NavDropdown>
              </Nav>
            </Navbar.Collapse>
          </Container>
        </Navbar>

      <Switch>

        <Route exact path="/">
          <div className="jumbotron background">
            <h1>20% Season Off</h1>
            <p>
              This is a simple hero unit, a simple jumbotron-style component for calling
              extra attention to featured content or information.
            </p>
            <p>
              <Button variant="primary">Learn more</Button>
            </p>
          </div>
          <Container>
            <Row>
              <Shoes shoes={shoes} />

            </Row>
            <button className="btn btn-primary" onClick={()=>{

              axios.get('http://codingapple1.github.io/shop/data2.json')
              .then((result)=>{
                shoes_chg([...shoes, ...result.data])
              })
              .catch(()=>{
                console.log('실패했어요')
              })
            }}>더보기</button>

          </Container>
        </Route>

          
        <Route path="/detail/:id">
          <Detail shoes={shoes} stock={재고} stockchg={재고변경} />
        </Route>

        <Route path="/:id">
          <div>아무거나 적었을때 이거 보여줘</div>
        </Route>

      </Switch>


    </div>
  );
}





function Shoes(props) {
  return (
    props.shoes.map((v,i) =>{
      return (
        <Col md={4} key={i}>
          <img src={`https://codingapple1.github.io/shop/shoes${i+1}.jpg`} width="100%" alt={`shoes${i+1}`} />
          <h4>{v.title}</h4>
          <p>{v.content}</p>
          <p>{v.price}</p>
        </Col>
      )
    })
  )
}

export default App;
